<template>
    <div>
        <div class="card p-3">
            <label for="" class="form-label fw-semibold text-start"><span class="text-primary fw-semibold ">{{ currentQuestion+1 }} | </span>{{questions[currentQuestion].label}}</label>
            <input  v-if="questions[currentQuestion].type==='text'" @keyup.enter="nextQuestion(currentQuestion+1)" v-model="userReviews[currentQuestion]" type="text" class="input-text mt-2">
            <input  v-if="questions[currentQuestion].type==='date'" @keyup.enter="nextQuestion(currentQuestion+1)" v-model="userReviews[currentQuestion]" type="date" class="input-text mt-2">
            <input  v-if="questions[currentQuestion].type==='number'" @keyup.enter="nextQuestion(currentQuestion+1)" min="0" max="100" step="1" v-model="userReviews[currentQuestion]" type="number" class="input-text mt-2">
        </div>
        <div class="progress mt-3" style="height:10px;" role="progressbar" aria-label="Basic example" aria-valuenow="0" aria-valuemin="0" aria-valuemax="100">
            <div class="progress-bar" :style="{width: (currentQuestion+1) / (questions.length/100) + '%'}"></div>
          </div>
        <div class="d-flex justify-content-between align-items-center mt-3">
            <button @click="nextQuestion(currentQuestion-1)" :disabled="currentQuestion===0" class="btn btn-primary rounded-2"><i class="fa-solid fa-arrow-left me-2"></i>Previous</button>
            <span >Review  {{ currentQuestion+1 }}/{{ questions.length }}</span>
            <button @click="nextQuestion(currentQuestion+1)" :hidden="currentQuestion+1===questions.length" class="btn rounded-2 btn-primary">Next<i class="fa-solid fa-arrow-right ms-2"></i></button>
            <button  @click="addToTable()" :hidden="currentQuestion+1!==questions.length" class="btn rounded-2 btn-primary">Add to Table<i class="fa-solid fa-plus-circle ms-2"></i></button>
        </div>
    </div>
</template>

<script>

import Swal from 'sweetalert2/dist/sweetalert2.min.js';

export default {
    name: 'SingleCreateInput',
    props: {
        option: {
            type: String,
        },
    },
    data() {
        return {
            answer : '',
            GdQuestions: [
                {type:'text',label:'Enter Graphic Designer Name :'},
                {type:'text',label:'Enter Assign Title :'},
                {type:'date',label:'Enter Graphic Sent Date :'},
                {type:'number',label:'Rate his/her creativity (0-100)'},
                {type:'number',label:'how much you satisfied ? (0-100)'},
                {type:'number',label:'Correction Percentage (0-100)'}
            ],
            CwQuestions: [
                {type:'text',label:'Enter Content Writer Name :'},
                {type:'text',label:'Enter Assign Title :'},
                {type:'date',label:'Enter Content Sent Date :'},
                {type:'number',label:'Rate his/her creativity (0-100)'},
                {type:'number',label:'how much you satisfied ? (0-100)'},
                {type:'number',label:'Correction Percentage (0-100)'}
            ],
            currentQuestion : 0,
            userReviews : [],
            validateStatus :false,
        }
    },   
    computed: {
        questions(){
            if(this.option === 'gd'){
                return this.GdQuestions;
            }else if(this.option === 'cw'){
                return this.CwQuestions;
            }
            return this.GdQuestions;
        }
    },
    methods: {
        nextQuestion(newQuestion) {
            if((this.userReviews[this.currentQuestion] || newQuestion < this.currentQuestion) && newQuestion < this.questions.length){
                this.currentQuestion = newQuestion;
            }else if(newQuestion === this.questions.length) {
                this.addToTable();
            }
        },
        addToTable(){
            if(this.userReviews.length>5 && this.userReviews[this.currentQuestion] !== ''){
                let finalReviews=[this.getCurrentDate(),...this.userReviews]; // -> get final review
                this.$store.commit("pushReview",finalReviews);
                this.resetReview();
                Swal.fire({
                title: 'Review added',
                text: "Your review is added to table",
                icon: 'success',
                showCancelButton: true,
                confirmButtonColor: '#0d6efd',
                cancelButtonColor: '#42b983',
                confirmButtonText: 'Review again',
                cancelButtonText: 'Go to Table'
                }).then((result) => {
                if (result.isConfirmed) {
                    this.resetReview();
                }else if (result.dismiss === Swal.DismissReason.cancel) {
                    this.$router.push('/html2pdf');
                }
                })
            }

        },
        resetReview() {
            this.answer = '';
            this.currentQuestion = 0;
            this.userReviews = [];
        },
        getCurrentDate() {
            const date = new Date();
            const options = { month: 'long', day: 'numeric' };
            return date.toLocaleDateString(undefined, options);
        },
    },
}

</script>

<style>
.input-text{
    width: 100%;
    outline: none;
    border: 1px solid gray;
    border-radius: 5px;
    padding: .6rem;
}

.input-text:focus {
    border: 2px solid #222222;
}
</style>